input,
textarea,
select {
  /* Remove the stupid outer glow in Webkit */
  outline: 0; }

/* Button Controls
-----------------------------------------------*/
input[type='checkbox'], input[type='radio'] {
  width: 13px;
  height: 13px; }

input[type='search'] {
  /* Make webkit render the search input like a normal text field */
  appearance: textfield;
